'use client';

import LocationMapClient from "../components/LocationMapClient";
import VoteBarChart from "../components/VoteBarChart";
import { useEffect, useState } from "react";
import RegionSelector from "../components/RegionSelector";

const dummyData = [
    { name: "Candidate A", votes: 120 },
    { name: "Candidate B", votes: 90 },
    { name: "Candidate C", votes: 60 },
];

export default function AdminPage() {
    const latitude = 23.2003; //
    const longitude = 79.8819;

    const [stars, setStars] = useState<
        { x: number; y: number; size: number; duration: number; delay: number }[]
    >([]);

    useEffect(() => {
        const newStars = Array.from({ length: 100 }, () => ({
            x: Math.random() * 100,
            y: Math.random() * 100,
            size: 1 + Math.random() * 2,
            duration: 10 + Math.random() * 10,
            delay: Math.random() * 5,
        }));
        setStars(newStars);
    }, []);

    return (
        <div className="relative min-h-screen bg-black text-white overflow-hidden">
            {/* Starfield Background */}
            <div aria-hidden="true" className="absolute inset-0 z-0">
                {stars.map((star, index) => (
                    <div
                        key={index}
                        className="absolute rounded-full bg-white opacity-80 animate-starfall"
                        style={{
                            left: `${star.x}%`,
                            top: `${star.y}%`,
                            width: `${star.size}px`,
                            height: `${star.size}px`,
                            animationDuration: `${star.duration}s`,
                            animationDelay: `${star.delay}s`,
                        }}
                    />
                ))}
            </div>

            {/* Main Content */}
            <div className="relative z-10 flex flex-col items-center justify-center mt-10">
                <div className="flex text-8xl font-bold animate-fade-up">
                    <span className="text-white glow-text pt-3">Vote</span>
                    <div className="text-blue-600 glow-text text-9xl">X</div>
                </div>
            </div>
            <div>
                <button className="absolute top-20 right-16 flex items-center px-6 py-3 overflow-hidden font-medium transition-all bg-indigo-500 rounded-md group">
                    {/* Top-right decoration */}
                    <span
                        className="absolute top-0 right-0 inline-block w-4 h-4 transition-all duration-500 ease-in-out bg-indigo-700 rounded group-hover:-mr-4 group-hover:-mt-4"
                    >
                        <span
                            className="absolute top-0 right-0 w-5 h-5 rotate-45 translate-x-1/2 -translate-y-1/2 bg-white"
                        ></span>
                    </span>

                    {/* Bottom-left decoration */}
                    <span
                        className="absolute bottom-0 rotate-180 left-0 inline-block w-4 h-4 transition-all duration-500 ease-in-out bg-indigo-700 rounded group-hover:-ml-4 group-hover:-mb-4"
                    >
                        <span
                            className="absolute top-0 right-0 w-5 h-5 rotate-45 translate-x-1/2 -translate-y-1/2 bg-white"
                        ></span>
                    </span>

                    {/* Hover background animation */}
                    <span className="absolute inset-0 w-full h-full transition-all duration-500 ease-in-out delay-200 -translate-x-full bg-indigo-600 rounded-md group-hover:translate-x-0"></span>

                    {/* Button Text */}
                    <span className="relative cursor-pointer     text-white font-bold transition-colors duration-200 ease-in-out group-hover:text-white z-10">
                        Live Voting
                    </span>
                </button>

            </div>
            <div className="relative z-10 flex w-full">
                <div className="flex w-full justify-evenly pt-5">
                    <div className="flex">
                        <LocationMapClient latitude={latitude} longitude={longitude} />
                    </div>

                    <div className="flex">
                        <VoteBarChart data={dummyData} />
                    </div>
                </div>
            </div>
        </div>
    );
}
